/* Grid
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.container {
  position: relative;
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
}

.column,
.columns {
  width: 100%;
  float: left;
  box-sizing: border-box;
}

/* For devices larger than 400px */
@media (min-width: 400px) {
  .container {
    width: 95%;
    padding: 0;
  }
}

/* For devices larger than 550px */
@media (min-width: 720px) {
  .container {
    width: 95%;
  }
  .column,
  .columns {
    margin-left: 4%;
  }
  .column:first-child,
  .columns:first-child {
    margin-left: 0;
  }
  .one.column,
  .one.columns {
    width: 4.66666666667%;
  }
  .two.columns {
    width: 13.3333333333%;
  }
  .three.columns {
    width: 22%;
  }
  .four.columns {
    width: 30.6666666667%;
  }
  .five.columns {
    width: 39.3333333333%;
  }
  .six.columns {
    width: 48%;
  }
  .seven.columns {
    width: 56.6666666667%;
  }
  .eight.columns {
    width: 65.3333333333%;
  }
  .nine.columns {
    width: 74.0%;
  }
  .ten.columns {
    width: 82.6666666667%;
  }
  .eleven.columns {
    width: 91.3333333333%;
  }
  .twelve.columns {
    width: 100%;
    margin-left: 0;
  }
  .one-third.column {
    width: 30.6666666667%;
  }
  .two-thirds.column {
    width: 65.3333333333%;
  }
  .one-half.column {
    width: 48%;
  }
  /* Offsets */
  .offset-by-one.column,
  .offset-by-one.columns {
    margin-left: 8.66666666667%;
  }
  .offset-by-two.column,
  .offset-by-two.columns {
    margin-left: 17.3333333333%;
  }
  .offset-by-three.column,
  .offset-by-three.columns {
    margin-left: 26%;
  }
  .offset-by-four.column,
  .offset-by-four.columns {
    margin-left: 34.6666666667%;
  }
  .offset-by-five.column,
  .offset-by-five.columns {
    margin-left: 43.3333333333%;
  }
  .offset-by-six.column,
  .offset-by-six.columns {
    margin-left: 52%;
  }
  .offset-by-seven.column,
  .offset-by-seven.columns {
    margin-left: 60.6666666667%;
  }
  .offset-by-eight.column,
  .offset-by-eight.columns {
    margin-left: 69.3333333333%;
  }
  .offset-by-nine.column,
  .offset-by-nine.columns {
    margin-left: 78.0%;
  }
  .offset-by-ten.column,
  .offset-by-ten.columns {
    margin-left: 86.6666666667%;
  }
  .offset-by-eleven.column,
  .offset-by-eleven.columns {
    margin-left: 95.3333333333%;
  }
  .offset-by-one-third.column,
  .offset-by-one-third.columns {
    margin-left: 34.6666666667%;
  }
  .offset-by-two-thirds.column,
  .offset-by-two-thirds.columns {
    margin-left: 69.3333333333%;
  }
  .offset-by-one-half.column,
  .offset-by-one-half.columns {
    margin-left: 52%;
  }
}

.row:after {
  content: " ";
  display: block;
  height: 0;
  clear: both;
}

/* Base Styles
–––––––––––––––––––––––––––––––––––––––––––––––––– */
/* NOTE
html is set to 62.5% so that all the REM measurements throughout Skeleton
are based on 10px sizing. So basically 1.5rem = 15px :) */
html {
  font-size: 62.5%;
}

body {
  line-height: 1.6;
  font-weight: 400;
  color: #222;
}

/* Typography
–––––––––––––––––––––––––––––––––––––––––––––––––– */
h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 2rem;
  font-weight: 300;
}

h1 {
  font-size: 4.0rem;
  line-height: 1.2;
  letter-spacing: -.1rem;
}

h2 {
  font-size: 3.6rem;
  line-height: 1.25;
  letter-spacing: -.1rem;
}

h3 {
  font-size: 3.0rem;
  line-height: 1.3;
  letter-spacing: -.1rem;
}

h4 {
  font-size: 2.4rem;
  line-height: 1.35;
  letter-spacing: -.08rem;
}

h5 {
  font-size: 1.8rem;
  line-height: 1.5;
  letter-spacing: -.05rem;
}

h6 {
  font-size: 1.5rem;
  line-height: 1.6;
  letter-spacing: 0;
}

/* Larger than phablet */
@media (min-width: 550px) {
  h1 {
    font-size: 5.0rem;
  }
  h2 {
    font-size: 4.2rem;
  }
  h3 {
    font-size: 3.6rem;
  }
  h4 {
    font-size: 3.0rem;
  }
  h5 {
    font-size: 2.4rem;
  }
  h6 {
    font-size: 1.5rem;
  }
}

p {
  font-size: 1.6rem;
  margin-top: 0;
}

.center {
  text-align: center;
}

.left {
  text-align: left;
}

.right {
  text-align: right;
}

.block {
  display: block;
}

body {
  font-family: 'Lato', sans-serif;
  font-size: 62.5%;
  background-color: #fff;
}

a {
  color: #434343;
  text-decoration: none;
}

h2 {
  display: inline-block;
  margin: 25px auto 50px;
  padding: 15px 0px;
  border-bottom: 5px solid #3FC0AA;
  font-weight: 300;
}

section {
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.card {
  position: relative;
  margin-bottom: 40px;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 5px 16px rgba(0, 0, 0, 0.06);
}

.card::after {
  content: '';
  position: absolute;
  display: inline-block;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 4px;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.15), 0 7px 25px rgba(0, 0, 0, 0.11);
  opacity: 0;
  -webkit-transition: opacity 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  -moz-transition: opacity 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  transition: opacity 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.card:hover::after {
  opacity: 1;
}

.card .card-header {
  background-color: #fafafa;
  background: #8e9eab;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #fafafa, #f9f9f9);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #fafafa, #f9f9f9);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.card .card-header h3 {
  padding: 20px 0px;
  margin: 0;
}

.card .card-body {
  text-align: left;
  padding: 18px 25px 8px;
  min-height: 200px;
  background-color: #fff;
}

.card .card-body p {
  color: rgba(0, 0, 0, 0.6);
}

header {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  border-top: 5px solid #3FC0AA;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.navigation {
  padding: 10px 0px;
  text-align: center;
}

.navigation .nav-icon {
  display: inline-block;
  float: left;
  width: 24px;
  height: 1px;
  margin-right: 5px;
  vertical-align: middle;
}

@media screen and (max-width: 480px) {
  .navigation .nav-icon {
    margin-right: 2px;
  }
}

.navigation .nav-icon svg {
  fill: #676767;
}

.navigation ul {
  display: inline-block;
  margin: 0 auto;
  padding: 0;
}

.navigation ul li {
  position: relative;
  display: inline-block;
  list-style: none;
}

.navigation ul li a {
  display: inline-block;
  margin: 0px 15px;
  padding: 7px 40px;
  font-size: 1.6rem;
  color: #434343;
  text-decoration: none;
  background: transparent;
  vertical-align: middle;
}

.navigation ul li a:hover ~ .tool-tip {
  opacity: 1;
}

@media screen and (max-width: 720px) {
  .navigation ul li a {
    padding: 7px 10px;
  }
}

@media screen and (max-width: 480px) {
  .navigation ul li a {
    padding: 7px 5px;
  }
}

.navigation ul li .tool-tip {
  opacity: 0;
  position: absolute;
  width: 70%;
  left: 0;
  right: 0;
  bottom: -30px;
  padding: 7px;
  margin: 0 auto;
  background: #908F8F;
  color: #fff;
  border-radius: 20px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.18);
  -webkit-transition: 222ms opacity cubic-bezier(0.445, 0.05, 0.55, 0.95);
  transition: 222ms opacity cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.navigation ul li .tool-tip::after {
  content: '';
  position: absolute;
  margin: 0 auto;
  width: 0;
  height: 0;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-bottom: 7px solid #908F8F;
  top: -7px;
  left: 0;
  right: 0;
}

.main {
  padding: 180px 0px 170px;
  background-image: radial-gradient(#fff, #F5F6FA);
}

@media screen and (max-width: 560px) {
  .main {
    padding: 120px 0px 70px;
  }
}

.main img {
  width: 500px;
  margin-bottom: 35px;
}

@media screen and (max-width: 560px) {
  .main img {
    width: 240px;
    margin-bottom: 5px;
  }
}

.main-header {
  font-size: 6.5rem;
  font-weight: 300;
}

@media screen and (max-width: 720px) {
  .main-header {
    font-size: 5rem;
  }
}

@media screen and (max-width: 560px) {
  .main-header {
    font-size: 3.2rem;
  }
}

.main-button {
  display: inline-block;
  margin: 0 auto;
  padding: 15px 50px;
  border-radius: 50px;
  text-decoration: none;
  background-color: #3FC0AA;
  color: #fff;
  font-size: 1.6rem;
  border-right: 2px solid #47BDA9;
  border-bottom: 2px solid #47BDA9;
  transition: background-color 300ms ease;
  background: #4ACEB7;
  background: -webkit-gradient(linear, left top, right bottom, color-stop(1%, #4ACEB7), color-stop(100%, #3FC0AA));
  background: -webkit-linear-gradient(-75deg, #4ACEB7 1%, #3FC0AA 100%);
  background: -webkit-linear-gradient(315deg, #4ACEB7 1%, #3FC0AA 100%);
  background: linear-gradient(135deg, #4ACEB7 1%, #3FC0AA 100%);
}

.main-button:hover {
  background-color: #4ACEB7;
}

.main-subtitle {
  margin: 25px auto;
  font-size: 1.8rem;
  color: rgba(0, 0, 0, 0.6);
}

@media screen and (max-width: 560px) {
  .main-subtitle {
    font-size: 1.6rem;
  }
}

.features {
  padding: 30px 0px 80px;
  background-color: #fff;
  color: #434343;
}

@media screen and (max-width: 720px) {
  .features {
    padding: 20px 0px 60px;
  }
}

@media screen and (max-width: 480px) {
  .features {
    padding: 0px;
  }
}

.features .container {
  padding: 25px;
}

.contributing {
  background-color: #D2F3EC;
  padding: 75px 0px 130px;
}

@media screen and (max-width: 720px) {
  .contributing {
    padding: 20px 0px 60px;
  }
}

@media screen and (max-width: 480px) {
  .contributing {
    padding: 10px 0px 50px;
  }
}

.contributing .cont-button {
  margin: 25px auto 0px;
}

.getting-started {
  padding: 50px 0px 130px;
  background-image: radial-gradient(#fafafa, #D2F3EC);
}

@media screen and (max-width: 720px) {
  .getting-started {
    padding: 40px 0px 100px;
  }
}

@media screen and (max-width: 480px) {
  .getting-started {
    padding: 20px 0px 50px;
  }
}

.getting-started .code-setup {
  padding: 30px 50px 40px;
  color: #2795EE;
  background-color: #fff;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 5px 16px rgba(0, 0, 0, 0.06);
  -webkit-transition: box-shadow 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  -moz-transition: box-shadow 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  transition: box-shadow 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

@media screen and (max-width: 720px) {
  .getting-started .code-setup {
    padding: 20px 30px 40px;
  }
}

.getting-started .code-setup p {
  margin: 25px 0px 10px;
  color: #A0A1A7;
  font-size: 15px;
}

.getting-started .code-setup input {
  width: 100%;
  border: none;
  font-family: monospace, sans-serif;
  font-size: 16px;
}

.footer {
  padding: 25px 0px 0px;
  color: rgba(0, 0, 0, 0.3);
}
